const DimensionBar = ({ label1, score1, label2, score2, color }) => {
  const total = score1 + score2;
  const percentage1 = total === 0 ? 50 : Math.round((score1 / total) * 100);
  const percentage2 = total === 0 ? 50 : Math.round((score2 / total) * 100);

  return (
    <Box sx={{ mb: 4, position: 'relative' }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1.5 }}>
        <Typography variant="subtitle2" sx={{ 
          fontWeight: 600, 
          color: COLORS.text.primary,
          fontSize: '0.875rem'
        }}>
          {label1} ({score1})
        </Typography>
        <Typography variant="subtitle2" sx={{ 
          fontWeight: 600, 
          color: COLORS.text.primary,
          fontSize: '0.875rem'
        }}>
          {label2} ({score2})
        </Typography>
      </Box>
      <Box
        sx={{
          display: 'flex',
          height: '12px',
          borderRadius: '6px',
          overflow: 'hidden',
          background: alpha(color, 0.1),
        }}
      >
        <Box
          sx={{
            width: `${percentage1}%`,
            background: color,
            transition: 'width 0.8s cubic-bezier(0.4, 0, 0.2, 1)',
          }}
        />
        <Box
          sx={{
            width: `${percentage2}%`,
            background: alpha(color, 0.3),
            transition: 'width 0.8s cubic-bezier(0.4, 0, 0.2, 1)',
          }}
        />
      </Box>
      <Box sx={{ 
        display: 'flex', 
        justifyContent: 'space-between', 
        mt: 0.5,
        opacity: 0.7,
        fontSize: '0.75rem'
      }}>
        <Typography variant="caption" sx={{ color: COLORS.text.secondary }}>
          {percentage1}%
        </Typography>
        <Typography variant="caption" sx={{ color: COLORS.text.secondary }}>
          {percentage2}%
        </Typography>
      </Box>
    </Box>
  );
};

const calculateDimensionScores = (answers) => {
  // 01
  const questionGroups = {
    EI: [0,4,8,12,16,20,24,28,32,36,40,44],  // 1,5,9,13,17,21,25,29,33,37,41,45
    SN: [1,5,9,13,17,21,25,29,33,37,41,45],  // 2,6,10,14,18,22,26,30,34,38,42,46
    TF: [2,6,10,14,18,22,26,30,34,38,42,46], // 3,7,11,15,19,23,27,31,35,39,43,47
    JP: [3,7,11,15,19,23,27,31,35,39,43,47]  // 4,8,12,16,20,24,28,32,36,40,44,48
  };

  // 
  const scores = {
    E: 0, I: 0, S: 0, N: 0, T: 0, F: 0, J: 0, P: 0
  };

  // 
  Object.entries(questionGroups).forEach(([dimension, questions]) => {
    questions.forEach(questionIndex => {
      const answer = answers[questionIndex];
      if (answer && typeof answer.A === 'number' && typeof answer.B === 'number') {
        switch(dimension) {
          case 'EI':
            scores.E += answer.A;
            scores.I += answer.B;
            break;
          case 'SN':
            scores.S += answer.A;
            scores.N += answer.B;
            break;
          case 'TF':
            scores.T += answer.A;
            scores.F += answer.B;
            break;
          case 'JP':
            scores.J += answer.A;
            scores.P += answer.B;
            break;
        }
      }
    });
  });

  return scores;
};

const calculateMBTIType = (scores) => {
  if (!scores) return null;
  
  return [
    scores.E > scores.I ? 'E' : 'I',
    scores.S > scores.N ? 'S' : 'N',
    scores.T > scores.F ? 'T' : 'F',
    scores.J > scores.P ? 'J' : 'P'
  ].join('');
};

const ResultPage = ({ result = null, userName }) => {
  const theme = useTheme();
  const resultRef = useRef(null);
  
  // 
  const exportAsImage = async () => {
    try {
      // 
      const button = document.querySelector('#export-button');
      if (button) {
        button.disabled = true;
        button.textContent = '...';
      }

      const element = resultRef.current;
      const canvas = await html2canvas(element, {
        scale: 2, // 
        useCORS: true,
        logging: false,
        backgroundColor: null,
        windowWidth: element.scrollWidth,
        windowHeight: element.scrollHeight
      });

      // 
      const image = canvas.toDataURL('image/png', 1.0);
      const link = document.createElement('a');
      link.download = `${userName ? userName + '-' : ''}MBTI.png`;
      link.href = image;
      link.click();

      // 
      if (button) {
        button.disabled = false;
        button.textContent = '';
      }
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  };

  // 
  const dimensionScores = result ? calculateDimensionScores(result) : null;
  const mbtiType = dimensionScores ? calculateMBTIType(dimensionScores) : null;
  const personality = mbtiType ? personalityDescriptions[mbtiType] : null;

  console.log('Result:', result);
  console.log('Dimension Scores:', dimensionScores);
  console.log('MBTI Type:', mbtiType);

  if (!personality || !dimensionScores) {
    return (
      <Container maxWidth="sm" sx={{ textAlign: 'center', py: 8 }}>
        <Typography variant="h4" color="error" gutterBottom>
          
        </Typography>
        <Typography variant="body1" color="textSecondary">
          MBTI
        </Typography>
        <Button
          variant="contained"
          onClick={() => window.location.reload()}
          sx={{ mt: 3 }}
        >
          
        </Button>
      </Container>
    );
  }

  return (
    <Box
      ref={resultRef}
      sx={{
        minHeight: '100vh',
        background: COLORS.background,
        py: 6,
        px: 2,
      }}
    >
      <Container maxWidth="lg">
        <Grid container spacing={4}>
          {/*  */}
          <Grid item xs={12}>
            <StyledCard>
              <CardContent sx={{ textAlign: 'center', py: 6 }}>
                <StyledAvatar>
                  <Psychology sx={{ fontSize: 40, color: COLORS.cardBg }} />
                </StyledAvatar>
                <Typography variant="h4" gutterBottom sx={{ 
                  fontWeight: 700, 
                  color: COLORS.text.primary,
                  mb: 2,
                  letterSpacing: '-0.5px'
                }}>
                  {userName ? `${userName}` : ''}
                </Typography>
                <Typography variant="h5" sx={{ 
                  mb: 3, 
                  color: COLORS.text.secondary,
                  fontWeight: 500,
                  letterSpacing: '-0.3px'
                }}>
                  {personality.title}
                </Typography>
                <Typography variant="body1" sx={{ 
                  maxWidth: '800px', 
                  mx: 'auto', 
                  mb: 5,
                  color: COLORS.text.secondary,
                  lineHeight: 1.8,
                  fontSize: '1.1rem'
                }}>
                  {personality.brief}
                </Typography>
                <Button
                  id="export-button"
                  variant="contained"
                  size="large"
                  startIcon={<Download sx={{ color: COLORS.cardBg }} />}
                  onClick={exportAsImage}
                  sx={{ 
                    mt: 2,
                    px: 6,
                    py: 1.5,
                    borderRadius: '30px',
                    background: COLORS.accent,
                    boxShadow: `0 8px 32px ${alpha(COLORS.accent, 0.3)}`,
                    color: COLORS.cardBg,
                    fontSize: '1rem',
                    fontWeight: 500,
                    '&:hover': {
                      background: COLORS.accentDark,
                      boxShadow: `0 12px 48px ${alpha(COLORS.accent, 0.4)}`,
                    }
                  }}
                >
                  
                </Button>
              </CardContent>
            </StyledCard>
          </Grid>

          {/*  */}
          <Grid item xs={12}>
            <StyledCard>
              <CardContent sx={{ py: 4 }}>
                <Typography variant="h6" gutterBottom sx={{ 
                  mb: 4, 
                  fontWeight: 600,
                  color: COLORS.text.primary,
                  textAlign: 'center'
                }}>
                  
                </Typography>
                <Box sx={{ maxWidth: 700, mx: 'auto' }}>
                  <DimensionBar
                    label1=" (E)"
                    score1={dimensionScores.E}
                    label2=" (I)"
                    score2={dimensionScores.I}
                    color={COLORS.dimension.EI}
                  />
                  <DimensionBar
                    label1=" (S)"
                    score1={dimensionScores.S}
                    label2=" (N)"
                    score2={dimensionScores.N}
                    color={COLORS.dimension.SN}
                  />
                  <DimensionBar
                    label1=" (T)"
                    score1={dimensionScores.T}
                    label2=" (F)"
                    score2={dimensionScores.F}
                    color={COLORS.dimension.TF}
                  />
                  <DimensionBar
                    label1=" (J)"
                    score1={dimensionScores.J}
                    label2=" (P)"
                    score2={dimensionScores.P}
                    color={COLORS.dimension.JP}
                  />
                </Box>
              </CardContent>
            </StyledCard>
          </Grid>

          {/*  */}
          <Grid item xs={12} md={6}>
            <StyledCard>
              <CardContent>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
                  <Star sx={{ color: COLORS.accent, mr: 1 }} />
                  <Typography variant="h6"></Typography>
                </Box>
                <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
                  {personality.characteristics.map((trait, index) => (
                    <StyledChip key={index} label={trait} />
                  ))}
                </Box>
              </CardContent>
            </StyledCard>
          </Grid>

          {/*  */}
          <Grid item xs={12} md={6}>
            <StyledCard>
              <CardContent>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
                  <TrendingUp sx={{ color: COLORS.accent, mr: 1 }} />
                  <Typography variant="h6"></Typography>
                </Box>
                <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
                  {personality.strengths.map((strength, index) => (
                    <StyledChip key={index} label={strength} />
                  ))}
                </Box>
              </CardContent>
            </StyledCard>
          </Grid>

          {/*  */}
          <Grid item xs={12} md={6}>
            <StyledCard>
              <CardContent>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
                  <WorkOutline sx={{ color: COLORS.accent, mr: 1 }} />
                  <Typography variant="h6"></Typography>
                </Box>
                <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
                  {personality.careers.map((career, index) => (
                    <StyledChip key={index} label={career} />
                  ))}
                </Box>
              </CardContent>
            </StyledCard>
          </Grid>

          {/*  */}
          <Grid item xs={12} md={6}>
            <StyledCard>
              <CardContent>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
                  <EmojiObjects sx={{ color: COLORS.accent, mr: 1 }} />
                  <Typography variant="h6"></Typography>
                </Box>
                <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
                  {personality.growth.map((tip, index) => (
                    <StyledChip key={index} label={tip} />
                  ))}
                </Box>
              </CardContent>
            </StyledCard>
          </Grid>

          {/*  */}
          <Grid item xs={12}>
            <StyledCard>
              <CardContent>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
                  <Psychology sx={{ color: COLORS.accent, mr: 1 }} />
                  <Typography variant="h6"></Typography>
                </Box>
                <Typography variant="body1" sx={{ 
                  maxWidth: '800px', 
                  mx: 'auto', 
                  mb: 5,
                  color: COLORS.text.secondary,
                  lineHeight: 1.8,
                  fontSize: '1.1rem'
                }}>
                  {personality.description}
                </Typography>
              </CardContent>
            </StyledCard>
          </Grid>

          {/*  */}
          <Grid item xs={12} sx={{ textAlign: 'center', mt: 4 }}>
            <Button
              variant="contained"
              startIcon={<RestartAlt />}
              onClick={() => window.location.reload()}
              sx={{
                borderRadius: '50px',
                px: 4,
                py: 1.5,
                background: COLORS.accent,
                boxShadow: `0 8px 32px ${alpha(COLORS.accent, 0.3)}`,
                color: COLORS.cardBg,
                fontSize: '1rem',
                fontWeight: 500,
                '&:hover': {
                  background: COLORS.accentDark,
                  boxShadow: `0 12px 48px ${alpha(COLORS.accent, 0.4)}`,
                }
              }}
            >
              
            </Button>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

export default ResultPage;
