import { questionGroups, dimensionMap, questions } from '../data/questions';

export function calculateResult(answers) {
  // 
  const scores = {
    E: 0, I: 0,
    S: 0, N: 0,
    T: 0, F: 0,
    J: 0, P: 0
  };

  // 01
  const questionGroups = {
    EI: [0,4,8,12,16,20,24,28,32,36,40,44],  // 1,5,9,13,17,21,25,29,33,37,41,45
    SN: [1,5,9,13,17,21,25,29,33,37,41,45],  // 2,6,10,14,18,22,26,30,34,38,42,46
    TF: [2,6,10,14,18,22,26,30,34,38,42,46], // 3,7,11,15,19,23,27,31,35,39,43,47
    JP: [3,7,11,15,19,23,27,31,35,39,43,47]  // 4,8,12,16,20,24,28,32,36,40,44,48
  };

  // 
  Object.entries(questionGroups).forEach(([dimension, questions]) => {
    questions.forEach(questionIndex => {
      const answer = answers[questionIndex];
      if (answer && typeof answer.A === 'number' && typeof answer.B === 'number') {
        switch(dimension) {
          case 'EI':
            scores.E += answer.A;
            scores.I += answer.B;
            break;
          case 'SN':
            scores.S += answer.A;
            scores.N += answer.B;
            break;
          case 'TF':
            scores.T += answer.A;
            scores.F += answer.B;
            break;
          case 'JP':
            scores.J += answer.A;
            scores.P += answer.B;
            break;
        }
      }
    });
  });

  // 
  const type = [
    scores.E > scores.I ? 'E' : 'I',
    scores.S > scores.N ? 'S' : 'N',
    scores.T > scores.F ? 'T' : 'F',
    scores.J > scores.P ? 'J' : 'P'
  ].join('');

  return {
    type,
    scores
  };
}

export function getPersonalityDescription(type) {
  const descriptions = {
    ISTJ: "",
    ISFJ: "",
    INFJ: "",
    INTJ: "",
    ISTP: "",
    ISFP: "",
    INFP: "",
    INTP: "",
    ESTP: "",
    ESFP: "",
    ENFP: "",
    ENTP: "",
    ESTJ: "",
    ESFJ: "",
    ENFJ: "",
    ENTJ: ""
  };

  return descriptions[type] || "";
}
